import React from "react";
import CarouselWithClass from '../components/Carousel/CarouselComClasse'
import CarouselWithHooks from '../components/Carousel/CarouselComHooks'

export default function Carousel() {
  return (
    <div className="container-fluid">
      <h1>Carousel</h1>
      <h2>Carousel With Class</h2>
      <CarouselWithClass/>
      <h2>Carousel With Hooks</h2>
        <CarouselWithHooks/>
    </div>
  );
}
