import React, { useState } from "react";
import Display from "./Display";
import Button from "./Button";
import "./Calculator.css";
export default (props) => {
  const [calc, setCalc] = useState({ display: "0", equation: "" });

  function clear() {
    setCalc({ display: "0", equation: "" });
  }

  function numInput(e) {
      console.log(e);
    if (calc.equation.match(/[0-9\.]$/) && !calc.equation.includes("=")) {
      if (calc.equation.match(/[+\-*\/]/) == null) {
        let val = calc.equation + e.currentTarget.value;
        setCalc({
          display: val,
          equation: val,
        });
      } else {
        setCalc({
          display: calc.display + e.currentTarget.value,
          equation: calc.equation + e.currentTarget.value,
        });
      }
    } else if (calc.equation.match(/[+\-*\/]$/)) {
      let val = calc.equation + e.currentTarget.value;
      setCalc({
        display: e.currentTarget.value,
        equation: val,
      });
    } else if (
      (calc.display === "0" && e.currentTarget.value !== "0") ||
      calc.equation.includes("=")
    ) {
      setCalc({
        display: e.currentTarget.value,
        equation: e.currentTarget.value,
      });
    }
  }

  function decInput(e) {
    if (calc.equation === "" || calc.equation.includes("=")) {
      let val = "0.";
      setCalc({
        display: val,
        equation: val,
      });
    } else if (calc.equation.match(/[+\-*\/]$/)) {
      let val = "0.";
      setCalc({
        display: val,
        equation: calc.equation + val,
      });
    } else if (!calc.display.includes(".")) {
      setCalc({
        display: calc.display + e.currentTarget.value,
        equation: calc.equation + e.currentTarget.value,
      });
    }
  }

  function operInput(e) {
    if (calc.equation.includes("=")) {
      let val = calc.display;
      val += e.currentTarget.value;
      setCalc({ equation: val });
    } else {
      if (calc.equation !== "" && calc.equation.match(/[*\-\/+]$/) == null) {
        let val = calc.equation;
        val += e.currentTarget.value;
        setCalc({ equation: val });
      } else if (calc.equation.match(/[*\-\/+]$/) != null) {
        let val = calc.equation;
        val = val.substring(0, val.length - 1);
        val += e.currentTarget.value;
        setCalc({ equation: val });
      }
    }
  }

  function calculate() {
    if (calc.equation.includes("=")) {
      let val = `${calc.display} = ${calc.display}`;
      setCalc({
        equation: val,
      });
    } else if (
      calc.equation !== "" &&
      calc.equation.match(/[+\-*\/]/) != null &&
      calc.equation.match(/[+\-*\/]$/) == null
    ) {
      let result = Number.isInteger(eval(calc.equation))
        ? eval(calc.equation)
        : parseFloat(eval(calc.equation).toFixed(5));
      let val = calc.equation;
      val += ` = ${result}`;
      setCalc({
        display: result,
        equation: val,
      });
    }
  }

  return (
    <div className="container">
      <Display equation={calc.equation} display={calc.display} />
      <Button
        id="clear"
        display="AC"
        class="row-3 coll-1-3"
        click={() => clear()}
      />
      <Button
        id="divide"
        value="/"
        display="รท"
        class="oper row-3 coll-4"
        click={(e) => operInput(e)}
      />
      <Button
        id="seven"
        value="7"
        display="7"
        class="num row-4 coll-1"
        click={(e) => numInput(e)}
      />
      <Button
        id="eight"
        value="8"
        display="8"
        class="num row-4 coll-2"
        click={(e) => numInput(e)}
      />
      <Button
        id="nine"
        value="9"
        display="9"
        class="num row-4 coll-3"
        click={(e) => numInput(e)}
      />
      <Button
        id="multiply"
        value="*"
        display="ร"
        class="oper row-4 coll-4"
        click={(e) => operInput(e)}
      />
      <Button
        id="four"
        value="4"
        display="4"
        class="num row-5 coll-1"
        click={(e) => numInput(e)}
      />
      <Button
        id="five"
        value="5"
        display="5"
        class="num row-5 coll-2"
        click={(e) => numInput(e)}
      />
      <Button
        id="six"
        value="6"
        display="6"
        class="num row-5 coll-3"
        click={(e) => numInput(e)}
      />
      <Button
        id="subtract"
        value="-"
        display="โ"
        class="oper row-5 coll-4"
        click={(e) => operInput(e)}
      />
      <Button
        id="one"
        value="1"
        display="1"
        class="num row-6 coll-1"
        click={(e) => numInput(e)}
      />
      <Button
        id="two"
        value="2"
        display="2"
        class="num row-6 coll-2"
        click={(e) => numInput(e)}
      />
      <Button
        id="three"
        value="3"
        display="3"
        class="num row-6 coll-3"
        click={(e) => numInput(e)}
      />
      <Button
        id="add"
        value="+"
        display="+"
        class="oper row-6 coll-4"
        click={(e) => operInput(e)}
      />
      <Button
        id="zero"
        value="0"
        display="0"
        class="num row-7 coll-1-2"
        click={(e) => numInput(e)}
      />
      <Button
        id="decimal"
        value="."
        display="."
        class="num row-7 coll-3"
        click={(e) => decInput(e)}
      />
      <Button
        id="equals"
        value="="
        display="="
        class="oper row-7 coll-4"
        click={() => calculate()}
      />
    </div>
  );
};
