import React, { useState, useEffect } from "react";
import "./CompassClock.css";

export default (props) => {
  const [date, setDate] = useState({
    year: 2016,
    M_month: "Dec",
    month: 1,
    day: 1,
    week: 1,
    hour: 12,
    Minute: 1,
    second: 1,
  });

  const Months = [
    "Jan",
    "Feb",
    "Mar",
    "Apr",
    "May",
    "Jun",
    "Jul",
    "Aug",
    "Spt",
    "Oct",
    "Nov",
    "Dec",
  ];

  const Zodiac = [
    "Aries",
    "Taurus",
    "Gemini",
    "Cancer",
    "Leo",
    "Virgo",
    "Libra",
    "Scorpio",
    "Sagittarius",
    "Capricorn",
    "Aquarius",
    "Pisces",
  ];

  const array = (length) =>
    Array.from({ length })
      .map((v, k) => k)
      .map((x) => x + 1);

  useEffect(() => {
    setInterval(() => {
      let time = new Date();
      setDate({
        second: time.getSeconds(),
        Minute: time.getMinutes(),
        hour: time.getHours(),
        day: time.getDate(),
        week: time.getDay(),
        month: time.getMonth() + 1,
        M_month: Months[time.getUTCMonth()],
        year: Zodiac[time.getUTCFullYear()%12]
      });
    }, 1000);
  });

  return (
    <div className="Clock">
      <div className="msg">
        <div className="year">
          <span>{date.year}</span> /Year
        </div>
      </div>

      <div className="msg_month">{date.M_month}</div>

      {/* Months */}
      <div className="box">
        {array(12).map((x, index) => {
          return (
            <div
              key={index}
              className={`month item ${
                index === date.month - 1 ? "active" : ""
              }`}
              style={{
                transform: `rotate(${index * 30 - 30 * (date.month - 1)}deg)`,
              }}
            >
              {`${x} month`}
            </div>
          );
        })}

        {/* Days */}
        {array(30).map((x, index) => {
          return (
            <div
              key={index}
              className={`day item ${index === date.day - 1 ? "active " : ""}`}
              style={{
                transform: `rotate(${index * 12 - 12 * (date.day - 1)}deg)`,
              }}
            >
              {`${x} day`}
            </div>
          );
        })}

        {/* Week */}
        {array(7).map((x, index) => {
          return (
            <div
              key={index}
              className={`week item ${index === date.week - 1 ? "active" : ""}`}
              style={{
                transform: `rotate(${
                  index * (360 / 21) - (360 / 21) * (date.week - 1)
                }deg)`,
              }}
            >
              {`week ${x}`}
            </div>
          );
        })}

        {/* Hours */}
        {array(24).map((x, index) => {
          return (
            <div
              key={index}
              className={`hour item ${index === date.hour - 1 ? "active" : ""}`}
              style={{
                transform: `rotate(${
                  index * (360 / 24) - (360 / 24) * (date.hour - 1)
                }deg)`,
              }}
            >
              {`${x} hr`}
            </div>
          );
        })}

        {/* Minutes */}
        {array(60).map((x, index) => {
          return (
            <div
              key={index}
              className={`Minute item ${
                index === date.Minute - 1 ? "active" : ""
              }`}
              style={{
                transform: `rotate(${
                  index * (360 / 60) - (360 / 60) * (date.Minute - 1)
                }deg)`,
              }}
            >
              {`${x} min`}
            </div>
          );
        })}

        {/* Seconds */}
        {array(60).map((x, index) => {
          return (
            <div
              key={index}
              className={`second item ${
                index === date.second - 1 ? "active" : ""
              }`}
              style={{
                transform: `rotate(${
                  index * (360 / 60) - (360 / 60) * (date.second - 1)
                }deg)`,
              }}
            >
              {`${x} sec`}
            </div>
          );
        })}
      </div>
    </div>
  );
};
