import React, { useState } from "react";
import Countries from "../../data/Countries";
import "./AutoComplete.css";

export default (props) => {
  const [search, setSearch] = useState({ suggestions: [], text: "" });

  function Search(e) {
    const value = e.target.value;
    let suggestions = [];
    if (value.length > 0) {
      const regex = new RegExp(`^${value}`, "i");
      suggestions = Countries.sort().filter((v) => regex.test(v));
    }
    setSearch({ suggestions, text: value });
  }

  function selectedText(value) {
    setSearch({
      text: value,
      suggestions: [],
    });
  }

  function renderSuggestions() {
    let { suggestions } = search;
    if (suggestions.length === 0) {
      return null;
    }
    return (
      <ul>
        {suggestions.map((item, index) => (
          <li key={index} onClick={() => selectedText(item)}>
            {item}
          </li>
        ))}
      </ul>
    );
  }

  return (
    <div className="divAutoComplete">
      <div className="col-md-12">
        <input
          className="textAutoComplete"
          type="text"
          onChange={(e) => Search(e)}
          value={search.text}
          placeholder="Type a country"
        />
        {renderSuggestions()}
        <div className="col-md-12 divSuggestions">
          <span>Suggestions:</span>
        </div>
      </div>
    </div>
  );
};
