import React, {useState} from 'react'
import './weather.css'

const apiKey = "8ddceeacaf8b95fe943c88fc8389dee0";

export default props => {
    
    const [weather, setWeather] = useState({ 
        temperature: null,
        city: null,
        country: null,
        humidity: null,
        description: null,
        error: null})

    async function getWeather(e){
        e.preventDefault();
        const city = e.currentTarget.elements.city.value
        const country = e.currentTarget.elements.country.value 
        
        if(city && country){
    
            try{
                const api = await fetch(`https://cors-anywhere.herokuapp.com/http://api.openweathermap.org/data/2.5/weather?q=${city},${country}&appid=${apiKey}&units=metric`)
                const {main, sys, name, weather} = await api.json()
                setWeather({
                    city: name,
                    country: sys.country,
                    temperature: main.temp,
                    humidity: main.humidity,
                    description: weather[0].description,
                    error: ""
                })


            }
            catch(ex){
                setWeather({
                    temperature: null,
                    city: null,
                    country: null,
                    humidity: null,
                    description: null,
                    error: "Please, check the values!"
                }); 
            }
        }
        else{
            setWeather({
                temperature: null,
                city: null,
                country: null,
                humidity: null,
                description: null,
                error: "please enter a valid values."
              });
        }
    }
    

    return(
        <div className="boxWeather">
            <div className="row">          
                <div className="col-md-6 divWeather">
                    <div className="row div-form">
                        <div className="col-md-10">
                            <form className="form-row" onSubmit={(e) => getWeather(e)}>
                                <div className="col-md-4">
                                    <input name="city" type="text" placeholder="Type a city"></input>
                                </div>
                                <div className="col-md-4">
                                    <input name="country"  type="text" placeholder="Type a country"></input>                        
                                </div>
                                <div className="col-md-4">
                                    <button className="btn-orange">Get Weather</button>                        
                                </div>
                            </form>
                        </div>
                    </div>
                    <div className="row div-form">
                        <div className="col-md-10">
                            {weather.city && weather.country && (
                                <p className="weather__key"> 
                                    Location:{" "}
                                    <span className="weather__value">{" "}{weather.city}, {weather.country}</span>
                                </p>
                            )}

                            {weather.temperature && (
                                <p className="weather__key">
                                Temperature: <span className="weather__value"> {weather.temperature}</span>
                                </p>
                            )}

                            {weather.humidity && (
                                <p className="weather__key">
                                    Humidity: <span className="weather__value"> {weather.humidity} </span>
                                </p>
                            )}

                            {weather.description && (
                                <p className="weather__key">
                                    Condition: <span className="weather__value"> {weather.description}</span>
                                </p>
                            )}

                            {weather.error && <p className="weather__error">{weather.error}</p>}
                        </div>
                    </div>        
                </div>         
            </div>
    </div>
    )
}