import React, { useState } from "react";
import "./Vote.css";

export default (props) => {
  const [countries, setCountry] = useState([
    { contry: "USA", votes: 0 },
    { contry: "Italy", votes: 0 },
    { contry: "England", votes: 0 },
  ]);

  function vote(index) {
    let newVote = [...countries]
    newVote[index].votes ++       
    setCountry(newVote)
  }

  return (
    <div className="boxVote">
      {countries.map((country, index) => {
        return (
          <div  key={index}>
            <div className="row divVote">
              <div className="col-sm-6 col-md-4">{country.votes}</div>
              <div className="col-sm-6 col-md-4">{country.contry}</div>
              <div className="col-sm-12 col-md-4">
                <button className="btn-orange" onClick={() => vote(index)}>
                  Vote
                </button>
              </div>
            </div>  
            {index < (countries.length -1) && <hr className="separate" />}
          </div>
        );
      })}
    </div>
  );
};
