import React from 'react';
import GoogleMapReact from 'google-map-react';
import './map.css'

export default (props) =>{

    const CentralPark = ({ text }) => (
        <div style={{
          color: 'white', 
          background: 'green',
          padding: '15px 10px',
          display: 'inline-flex',
          textAlign: 'center',
          alignItems: 'center',
          justifyContent: 'center',
          borderRadius: '100%',
          transform: 'translate(-50%, -50%)'
        }}>
          {text}
        </div>       
      );

      
    const TimesSquare = ({ text }) => (
        <div style={{
          color: 'white', 
          background: 'red',
          padding: '15px 10px',
          display: 'inline-flex',
          textAlign: 'center',
          alignItems: 'center',
          justifyContent: 'center',
          borderRadius: '100%',
          transform: 'translate(-50%, -50%)'
        }}>
          {text}
        </div>       
      );

    return(
        <div className="divMap">
            <GoogleMapReact defaultCenter={{lat: 40.7834, lng: -73.9662}} defaultZoom={12}>
                <CentralPark lat="40.778848" lng="-73.968898" text={'Central Park'} />
                <TimesSquare lat="40.758896" lng="-73.985130" text={'Times Square'} />
            </GoogleMapReact>
        </div>
    )
}